.heading {
  font-family: sans-serif;
  text-align: center;
}

.input-tag {
  width: 80%;
  border: 0.2rem solid green;
  border-radius: 2rem;
  outline: none;
  padding: 0.5rem;
}

.display {
  padding: 1rem;
  font-weight: bold;
  font-size: 2rem;
}

.primarybtn {
  margin: "10px";
  cursor: "pointer";
  color: "white";
  border: "3px solid yellow";
  border-radius: "25px";
  height: "30px";
}

.head-tag{
  border: 3px solid yellow;
  background-color: green;
  border-radius: 25px;
  height: 30px;
  display: block;
  margin: auto;
  padding: 3px;
  color: white;
}

.emoji{
  font-size: 2rem;
  padding: 0.5rem;
  cursor: pointer;
  margin-top: 10px;
}
